  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 10px 60px 0px rgba(42, 102, 193, 0.15);
}
.teacher__vid_vid {
  width: 100%;
  height: 450px;
}
.teacher__descr {
  padding: 40px 34px 53px;
  display: grid;
  grid-template-columns: 18% 1fr;
  grid-column-gap: 33px;
}
.teacher__descr__left {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.teacher__descr__right_top {
  display: flex;
}
.teacher__descr__right_top img {
  margin-right: 7px;
  width: 20px;
  height: 20px;
}
.teacher__descr__right_top_text {
  color: #292c32;
  font-size: 15px;
  font-weight: 600;
}
.teacher__descr__right_time-wr, .teacher__descr__right_exp-wr {
  display: flex;
  padding: 8px 16px;
  border-radius: 20px;
  background: #ffdee4;
  align-items: center;
}
.teacher__descr__right_time-wr *, .teacher__descr__right_exp-wr * {
  flex-shrink: 0;
}
.teacher__descr__right_exp-wr {
  background: #fdecc8;
  margin-left: 16px;
}
.teacher__descr__right_main {
  margin-top: 25px;
}
.teacher__descr__right_main_top_name {
  color: #292c32;
  font-size: 20px;
  font-weight: 700;
  line-height: 22px; /* 110% */
}
.teacher__descr__right_main_top_country {
  color: #454b58;
  font-size: 14px;
  font-weight: 600;
  line-height: 182.85%; /* 25.599px */
