  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 17px;
  cursor: pointer;
}
.teacher__descr_chat-text {
  color: #f04973;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px; /* 133.333% */
  text-decoration-line: underline;
  margin-left: 7px;
  transition: 0.3;
